<template>
  <div>
    <input type="text" class="todo-input" placeholder="What needs you
    to be done" v-model="newTodo"
    @keyup.enter="addTodo">
    <div v-for="(todo,index) in todos" :key="todo.id"
         class="todo-item">
      <div class="todo-item-left">
        <input type="checkbox" v-model="todo.completed">
        <div v-if="!todo.editing" @dblclick="editTodo(todo)"
             class="todo-item-label" :class=" { completed : todo.completed}"
        >{{ todo.title }}</div>
        <input v-else class="todo-item-edit" type="text"
               v-model="todo.title" @blur="doneEdit(todo)" @keyup.enter="doneEdit(todo)"
               @keyup.esc="cancelEdit(todo)"
        v-focus>
      </div>
      <div class="remove-item" @click="removeTodo(index)">
        &times;
      </div>
    </div>
    <div class="extra-container">
      <div><label><input type="checkbox" :checked="!anyRemaining"
      @change="checkAllTodos">CheckAll</label></div>
      <div>{{remaining}} items left</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'todo-list',
  data() {
    return {
      newTodo: '',
      idForTodo: 3,
      beforeEditCache: '',
      todos: [
        {
          'id' : 1,
          'title': 'Finish Vue',
          'computed': false,
          'editing' : false,
        }
      ]
    }
  },
  computed: {
    remaining() {
      return this.todos.filter(todo=>!todo.completed).length
    },
    anyRemaining() {
      return this.remaining != 0
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods: {
    addTodo() {
      if (this.newTodo.trim().length == 0) {
        return
      }
      this.todos.push({
        id: this.idForTodo,
        title: this.newTodo,
        completed: false,

      })
      this.newTodo = ''
      this.idForTodo++
    },
    editTodo(todo) {
      this.beforeEditCache = todo.title
      todo.editing = true
    },
    doneEdit(todo) {
      if (todo.title.trim().length == 0) {
        todo.title = this.beforeEditCache
      }
      todo.editing = false
    },
    cancelEdit(todo) {
      todo.title = this.beforeEditCache
      todo.editing = false
    },
    removeTodo(index) {
      this.todos.splice(index,1)
    },
    checkAllTodos() {
      this.todos.forEach((todo) => todo.completed = event.target.checked)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.todo-input {
  width: 100%;
  padding: 10px 18px;
  font-size: 18px;
  margin-bottom: 16px;

  &:focus {
    outline: none;
  }
}
  .todo-item {

  }
.remove-item {
  cursor: pointer;
  &hover {
    color: black;
  }

}
.completed {
  text-decoration: line-through;
  color: grey;
}
button {
  &:hover {
    background: lightgreen;
  }
  &focus {
    outline: none;
  }
}
.active {
  background: lightgreen;
}
</style>